.top {
  margin-top: 8rem;
  display: flex;
  width: 100vw;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 2rem;
  overflow-x: hidden;
  font-weight: 700;
}
#right_img {
  height: auto;
  width: 40vw;
  display: flex;
  justify-content: center;
}
#top_head {
  font-size: 4vw;
  font-family: "Inter";
  text-align: center;
  margin-bottom: 10px;
}
#paragraph {
  width: 50vw;
  display: flex;
  flex-wrap: wrap;
  font-size: 0.5rem;
  text-align: center;
  padding: 20px;
}
.cardbox {
  display: flex;
  flex-direction: column;
  width: 100vw;
}
.row {
  width: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.card {
  position: relative;
  margin-bottom: 2rem;
  padding-left: 2rem;
  width: 40vw;
  height: 35vh;

  background: linear-gradient(
    155deg,
    rgb(11, 11, 146) 0%,
    rgba(0, 212, 255, 1) 100%
  );
  border-radius: 10px;
}
#web_img {
  position: absolute;
  height: auto;
  /* max-width: 12rem; */
  margin-left: 58%;
  margin-top: -20%;
  /* left: -1%; */
}
.register {
  position: absolute;

  width: 30%;
  height: 20%;
  font-size: 1rem;
  color: rgb(2, 5, 101);
  border-radius: 5px;
  border: none;
  transition: 0.15s all ease-in-out;
}
.register:hover {
  transform: scale(1.1);
}
#big {
  font-family: "Inter";
  font-size: 1.5rem;
  font-weight: 400;
  color: aliceblue;
  margin-top: 2vw;
  margin-bottom: 1vw;

  font-weight: 700;
}
#small {
  display: flex;
  font-family: "inter";
  flex-wrap: wrap;
  width: 50%;
  font-size: 0.1rem;
  color: aliceblue;
  margin-bottom: 1vw;
}

@media screen and (max-width: 1000px) {
  #web_img {
    margin-top: -15%;
  }
  .row {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .top {
    flex-direction: column;
  }
  .card {
    width: 50vw;
    margin-bottom: 2rem;
  }
  #paragraph {
    width: 80vw;
    font-size: 1.2rem;
  }
  .cardbox {
    display: flex;
    justify-content: center;
  }
}
