@import url("https://fonts.googleapis.com/css2?family=Inter&family=Kadwa&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Kaisei+Decol&family=Kalam:wght@300&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}

:root {
  --bg-color: #fff;
  --text-color: #000000;
  --main-color: #29fd53;
}

body {
  min-height: 100vh;
  background: var(--bg-color);
  color: var(--text-color);
}

body::-webkit-scrollbar {
  display: none;
}

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

a {
  color: black;
  text-decoration: none;
}

abbr {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

li {
  list-style: none;
}

a {
  color: black;
  text-decoration: none;
}

.home {
  position: relative;
  display: flex;
  min-height: 100vh;
  background-size: cover;
  background-position-y: 0px;
  background-attachment: fixed;
  font-family: "Ubuntu", sans-serif;
}

.company h1 {
  position: absolute;
  text-align: center;
  top: 30%;
  font-size: 170px;

  color: #000000;
  font-family: "Inknut Antiqua", serif;

  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  font-family: kadwa;
}

.company-quote {
  position: absolute;
  text-align: center;
  font-size: 40px;
  left: 0;
  right: 0;
  padding-top: 2%;
  margin-left: auto;
  margin-right: auto;
}

.main_heading {
  display: grid;
  place-items: center;
  font-family: "Kaisei Decol", serif;
  font-size: 45px;
  margin-top: 100px;
  margin-bottom: 45px;
  opacity: 0.7;
}

.about {
  position: relative;
  width: 100%;
  height: 100vw;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  padding: 10px;
}

.about img {
  display: none;
}

.about h1 {
  text-align: center;
  color: rgb(5, 99, 161);
  font-family: Kadwa;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  font-size: 80px;
  text-shadow: 0 0 3px rgb(97, 199, 247);
}

.about p {
  position: relative;
  margin-top: 30px;
  background: rgb(16, 203, 253);
  background: linear-gradient(
    90deg,
    rgba(16, 203, 253, 1) 0%,
    rgba(0, 212, 255, 1) 0%,
    rgba(17, 9, 121, 1) 100%
  );
  color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  font-family: "Kalam", cursive;
  width: 45%;
  font-size: 32px;
  left: 55%;
  overflow-y: auto;
  text-align: center;
  padding-top: 4rem;
}

.quotes p {
  text-align: center;
  padding: 60px 0;
  font-size: 200%;
  color: #ff0000;
}

.choose-us {
  color: rgb(5, 99, 161);
  font-family: kadwa;
}

.choose-us .choose-text {
  text-align: center;
  padding: 40px 0;
  background-color: rgba(255, 255, 255, 0.7);
  font-size: 20px;
}

.choose-us h2 {
  font-size: 50px;
}

.choose-us .choose-img {
  display: flex;
  justify-content: space-around;
  padding: 30px 0 50px 0;
}

.choose-img img:hover {
  animation-name: slideUp;
  animation-name: slideUp;
  animation-duration: 1s;
  animation-duration: 1s;
  visibility: visible;
  cursor: pointer;
}

@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(40%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

.img-one img {
  width: 120px;
}

.img-two p {
  width: 250px;
  margin-top: 15px;
}
.img-two img {
  width: 180px;
}
.img-three img {
  width: 100px;
  padding-top: 20px;
}
.img-four img {
  width: 120px;
}

.animate-char {
  margin-top: 100px;
  font-family: kadwa;
  text-align: center;
  font-size: 40px;
}

.animate-char {
  text-transform: uppercase;
  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 2s linear infinite;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

main {
  position: relative;
  width: calc(min(93rem, 84%));
  margin: 0 auto;
  min-height: 80vh;
  column-gap: 3rem;
  padding-block: min(11vh, 10rem);
}

