  }

  .flip-card-back h3 {
    font-size: 1.5vw;
    margin-bottom: -1vw;
  }

  .flip-card-back p {
    font-size: 1.3vw;
  }
}

@media (min-width: 1024px) and (max-width: 1280px) {
