    font-size: 1.2vw;
  }

  .flip-card-back h3 {
    font-size: 1.5vw;
    margin-bottom: -2vw;
  }

  .flip-card-back p {
    font-size: 1.3vw;
  }
}

/* 
   ##Device = Tablets, Ipads (portrait)
   ##Screen = B/w 768px to 1024px
 */

@media (min-width: 768px) and (max-width: 1023px) {
  .flip-card-front p {
    font-size: 1.2vw;
  }

  .flip-card-back h3 {
    font-size: 1.5vw;
    margin-bottom: -2vw;
  }

  .flip-card-back p {
    font-size: 1.3vw;
  }
}

@media (min-width: 425px) and (max-width: 767px) {
  .flip-card-front p {
    font-size: 1.2vw;
  }

  .flip-card-back h3 {
    font-size: 1.5vw;
    margin-bottom: -3vw;
  }

  .flip-card-back p {
    font-size: 1.3vw;
  }
}

/* 
   ##Device = Most of the Smartphones Mobiles (Portrait)
   ##Screen = B/w 320px to 479px
 */

@media (min-width: 320px) and (max-width: 425px) {
  .flip-card-front p {
    font-size: 1.2vw;
  }

  .flip-card-back h3 {
    font-size: 1.5vw;
    margin-bottom: -5vw;
  }

  .flip-card-back p {
    font-size: 1.3vw;
  }

  .selected-ambulance-features {
    height: 18vw;

    width: 18vw;
  }

  .flip-card {
    border-radius: 10vw;
  }
}

/* 
 ##Device = Desktops
 ##Screen = 1281px to higher resolution desktops
 */

@media (min-width: 769px) and (max-width: 1024px) {
  .underline {
    background-size: 30% 5px;
  }
}
/* 
 ##Device = Low Resolution Tablets, Mobiles (Landscape)
 ##Screen = B/w 481px to 767px
 */

@media (min-width: 481px) and (max-width: 768px) {
  .customer-support-figcaption {
    font-size: 2vw;
  }
  .underline {
    background-size: 30% 5px;
  }
}

/* 
 ##Device = Most of the Smartphones Mobiles (Portrait)
 ##Screen = B/w 320px to 479px
 */

@media (min-width: 320px) and (max-width: 480px) {
  .underline {
    background-size: 30% 3px;
  }

  .selected-ambulance-feature-p {
    font-size: 1.5vw;
  }
}

.next,
.prev {
  position: relative;
  display: inline-block;
  cursor: pointer;
  width: 40px;
  padding-bottom: 40px;
  border-radius: 50%;
  box-sizing: border-box;
  border: 3px solid #333;
}

.prev:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 40%;
  padding-bottom: 35%; /* Maintain a 1:1 aspect ratio */
  border-left: 4px solid #333;
  border-bottom: 4px solid #333;
  -webkit-transform: translate(-30%, -50%) rotate(45deg);
  transform: translate(-30%, -50%) rotate(45deg);
}

.next:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 40%;
  padding-bottom: 35%; /* Maintain a 1:1 aspect ratio */
  border-right: 4px solid #333;
  border-bottom: 4px solid #333;
  -webkit-transform: translate(-70%, -50%) rotate(-45deg);
  transform: translate(-70%, -50%) rotate(-45deg);
}
@media (max-width: 560px) {
  .next,
  .prev {
    position: relative;
    display: inline-block;
    cursor: pointer;
    width: 30px; /* "" width */
    padding-bottom: 30px; /* "" padding */
    border-radius: 50%;
    box-sizing: border-box;
    border: 3px solid #333;
  }

  .prev:before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 40%;
    padding-bottom: 25%;
    border-left: 4px solid #333;
    border-bottom: 4px solid #333;
    -webkit-transform: translate(-30%, -50%) rotate(45deg);
    transform: translate(-30%, -50%) rotate(45deg);
  }

  .next:before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 40%;
    padding-bottom: 25%;
    border-right: 4px solid #333;
    border-bottom: 4px solid #333;
    -webkit-transform: translate(-70%, -50%) rotate(-45deg);
    transform: translate(-70%, -50%) rotate(-45deg);
  }
}
.selected-ambulance-inner-img {
  border: 0.4vw solid black;
  border-radius: 1vw;
}
