.choose {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* margin-top: 2rem; */
}
#why_us {
  font-family: "Inknut Antiqua", serif;
  text-decoration: none;
  color: #000;
  font-size: 4rem;
  transition: 0.15s all ease-in-out;
}
#why_us:hover {
  transform: scale(1.05);
  background: -webkit-linear-gradient(0deg, #004081 0%, #429ecf 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.low {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100vw;
}
.partone,
.parttwo,
.partthree,
.partfour {
  display: flex;
  justify-content: space-around;
  width: 50vw;
  flex-direction: row;
  align-items: center;
}
.low img {
  height: auto;
  /* max-width: 12rem; */
}
.low abbr {
  height: inherit;
  width: inherit;
}
.low img:hover {
  transition: transform 1500ms;
  transform: rotateY(360deg);
  cursor: pointer;
}
.one,
.two,
.three,
.four {
  height: max-content;
  width: max-content;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.low a {
  text-decoration: none;
  font-weight: bolder;
  text-align: center;
  background: -webkit-linear-gradient(0deg, #48abe0 0%, #00478f 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

