.come {
  display: flex;
  margin-top: 3rem;
  align-items: center;
  justify-content: center;
}
.come a {
  font-weight: 900;
  font-family: "Inknut Antiqua", serif;
  font-size: 4rem;
  background: -webkit-linear-gradient(0deg, #48abe0 0%, #00478f 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: 0.15s all ease-in-out;
}
.come :hover {
  transform: scale(1.1);
  background: -webkit-linear-gradient(0deg, #00478f 0%, #48abe0 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.news1 {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
}
.news1 img {
  border-top-right-radius: 15%;
  border-bottom-left-radius: 15%;
  height: auto;
  width: 25vw;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  transition: 0.15s all ease;
}
.news2 {
  margin-top: 2dvh;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  width: 75vw;
  height: auto;
  margin-left: 12vw;
}
.news2 img {
  border-top-right-radius: 4rem;
  border-bottom-left-radius: 4rem;
  width: inherit;
  height: inherit;
  transition: 0.15s all ease;
}
.news1 img:hover,
.news2 img:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.whywecome {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100vw;
  height: max-content;
}

@media only screen and (max-width: 1270px) {
  .come a {
    font-size: 3rem;
  }
}

@media only screen and (max-width: 1120px) {
  .come a {
    font-size: 2rem;
  }
}
@media only screen and (max-width: 650px) {
  .news2 img {
    border-top-right-radius: 2rem;
    border-bottom-left-radius: 2rem;
  }
}
.news2,
.news1 {
  transition: opacity 0.25s; /* Adjust the duration for the fade effect */
}

.fade {
  opacity: 0;
  transition: opacity 0.25s; /* Should match the transition duration in .news1 */
}
